<template>
<div>
    <b-nav-text class="title">Videos</b-nav-text>
    <div class="grid-container">
    <div v-for="video in filterVideos()" :key="video.id" class="grid-item">
     <video-embed :src="video.videoUrl"></video-embed>

    </div>
    </div>
</div>
</template>

<script>
import Vue from 'vue'
import Embed from 'v-video-embed'
Vue.use(Embed);
import videosJSON from '../videos.json'
const {videos} = videosJSON


export default {
    data(){
    return {
        videos
        }
    },
    methods: {
        filterVideos() {
            return this.videos.filter(video => video.id < 5)
        }
    }
}
</script>

<style scoped>

.title {
    font-size: 25px;
    color: white;
    padding-left: 20px;
}

.grid-container {
    height: 100%;
    width: 100%;
    display: grid;
    gap: 1%;
}

.grid-item {
    grid-row: 1/2;
}



</style>